


const allCheck = document.querySelector('#checkAll');
const allCheckBox = document.querySelectorAll("input[type='checkbox']");



// console.log(allCheckBox);

let isChecked = true;

allCheck.addEventListener('click',()=>{

  isChecked = !isChecked;
  allCheckBox.forEach(item =>{
    item.checked = isChecked;
  })
  checkConditions();
  });

  
  const force1 = document.querySelector('.force1');
  const force2 = document.querySelector('.force2');
  const nextBtn = document.querySelector('.nextbtn')
  

  function checkConditions(){

    if(force1.checked && force2.checked){
      nextBtn.style.cursor = "pointer";
      nextBtn.style.backgroundColor = 'royalblue';   
    }else{
      nextBtn.style.backgroundColor = '#777';
      nextBtn.style.cursor = "";
    }

  }
  
  force1.addEventListener('change', checkConditions);
  force2.addEventListener('change', checkConditions);
  
